<template>
  <v-container
    fluid
    grid-list-md>
    <v-form
      ref="form2"
      @submit.prevent="validateBeforeSubmit">
      <v-layout
        row
        wrap>
        <v-flex md12>
          <v-card class="elevation-0">
            <v-card-title>
              <h3 class="headline mb-0">Selecione as datas de vencimento e pagamento</h3>
            </v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md12>
                  <core-filtro-rede-empresa-loja
                    v-model="value"
                    :show-todas="true"
                    :on-change-rede="onChangeRede"
                    :on-change-empresa="onChangeEmpresa"
                    :loja-selecionada="lojaSelecionada"
                    :hidden-loja="true"
                  />
                </v-flex>
                <v-flex md2>
                  <v-autocomplete
                    v-model="value.tpRepasseId"
                    :items="repasses"
                    label="Tipo de Repasse"
                    item-value="id"
                    item-text="descricao"
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-model="value.vencimentoAte"
                    label="Vencimento até"
                    type="date"
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-model="value.pagamentoEm"
                    label="Pagamento em"
                    type="date"
                  />
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex md12>
          <v-layout class="justify-end">
            <v-btn
              :disabled="disableBtn"
              :loading="loadingBtn"
              type="submit"
              color="primary">Continuar</v-btn>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-form>
  </v-container>
</template>

<script>
export default {
  props: {
    value: {
      type: Object,
      required: true
    },
    repasses: {
      type: Array,
      required: true
    },
    // eslint-disable-next-line vue/require-default-prop
    setData: {
      type: Function,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    nextStep: {
      type: Function
    }
  },
  data () {
    return {
      loadingBtn: false
    }
  },
  computed: {
    disableBtn () {
      return !(this.value.vencimentoAte && this.value.pagamentoEm && this.value.tpRepasseId)
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.nextStep()
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    },
    onChangeRede (rede) {
      console.log('OnChangeRede')
      this.value.redeNome = rede.descricao
      this.clear()
    },
    onChangeEmpresa (empresa) {
      console.log('OnChangeEmpresa')
      this.value.empresaNome = empresa.descricao
      this.clear()
    },
    lojaSelecionada (loja) {
      this.value.lojaNome = loja.descricao
      this.value.lojaId = loja.id
    },
    clear () {
      this.value.lojaId = null
    }
  }
}
</script>
